.versionDocContainer{max-width:1200px;margin:0 auto;padding:2rem 1rem}.pageTitle{font-size:1.8rem;font-weight:600;margin-bottom:2rem;color:#333;text-align:center}.controlPanel{display:flex;gap:1rem;margin-bottom:1.5rem}.controlPanel select,.controlPanel input{padding:.5rem;font-size:1rem;border:1px solid #ccc;border-radius:4px}.resultContainer{display:flex;flex-direction:column;gap:1.5rem}.versionCard{padding:1.5rem;border-radius:8px;box-shadow:0 4px 12px rgba(0,0,0,.08);transition:transform .2s ease,box-shadow .2s ease;background-color:#fff;cursor:pointer}.versionCard:hover{transform:translateY(-2px);box-shadow:0 6px 16px rgba(0,0,0,.12)}.latestCard{border:2px solid #4a90e2;background-color:#f5faff}.versionHeader{display:flex;justify-content:space-between;align-items:center;margin-bottom:1rem;padding-bottom:.8rem;border-bottom:1px solid #eee}.versionTitle{font-size:1.3rem;font-weight:600;color:#2c5282}.versionDate{font-size:.9rem;color:#6b7280}.updateList{list-style-type:none;padding-left:.5rem}.updateItem{position:relative;padding-left:1.5rem;margin-bottom:.8rem;line-height:1.5}.updateItem:before{content:"•";position:absolute;left:0;color:#4299e1;font-weight:bold}.updateItem:last-child{margin-bottom:0}.noData{text-align:center;padding:2rem;font-size:1rem;color:#666}@media(min-width: 768px){.versionCard{padding:2rem}.pageTitle{font-size:2.2rem;margin-bottom:3rem}}.controlPanel{display:flex;gap:1rem;margin-bottom:1.5rem}.controlPanel select,.controlPanel input{padding:.5rem;font-size:1rem;border:1px solid #ccc;border-radius:4px}.versionTitleGroup{display:flex;align-items:center;gap:.5rem}.newBadge{background-color:#4299e1;color:#fff;font-size:.75rem;padding:2px 6px;border-radius:4px;font-weight:bold;letter-spacing:.5px}